.gpt3__footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  background: var(--color-footer);
  /* ff 3.6+ */
  background:-moz-radial-gradient(circle at 30% -140%, #042c54 15%, var(--color-bg) 80%, var(--color-footer) 180%);

  /* safari 5.1+,chrome 10+ */
  background:-webkit-radial-gradient(circle at 30% -140%, #042c54 15%, var(--color-bg) 80%, var(--color-footer) 180%);

  /* opera 11.10+ */
  background:-o-radial-gradient(circle at 30% -140%, #042c54 15%, var(--color-bg) 80%, var(--color-footer) 180%);

  /* ie 10+ */
  background:-ms-radial-gradient(circle at 30% -140%, #042c54 15%, var(--color-bg) 80%, var(--color-footer) 180%);

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 30% -140%, #042c54 15%, var(--color-bg) 80%, var(--color-footer) 180%);

  filter: drop-shadow(0px 7px 7px rgba(0, 0, 0, 0.25));
}

.gpt3__footer-heading {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}

.gpt3__footer-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  text-align: center;
  letter-spacing: -0.04em;
}

.gpt3__footer-btn {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 1rem;
  border: 0px solid white;
  text-align: center;
  margin-bottom: 10rem;
  cursor: pointer;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.35));
}

.gpt3__footer-btn p {
  box-sizing: border-box;
  font-family: var(--font-family);
  font-size: 18px;
  line-height: 21px;
  word-spacing: 2px;
  color: #ffffff;
}

.gpt3__footer-links {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
  text-align: left;
}

.gpt3__footer-links div {
  width: 250px;
  margin: 1rem;
}

.gpt3__footer-links_logo {
  display: flex;
  flex-direction: column;
}

.gpt3__footer-links_logo img {
  width: 118px;
  height: 30px;
  margin-bottom: 1rem;
}

.gpt3__footer-links_logo p {
  font-family: var(--font-family);
  font-size: 12px;
  line-height: 14px;
  color: white;
}

.gpt3__footer-links_div {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.gpt3__footer-links_div h4 {
  font-family: var(--font-family);
  font-size: 14px;
  line-height: 16px;
  color: white;
  margin-bottom: 1rem;
}

.gpt3__footer-links_div p {
  font-family: var(--font-family);
  font-size: 12px;
  line-height: 24px;
  color: white;

  margin-bottom: 0.5rem 0;
  cursor: pointer;
}

.gpt3__footer-copyright {
  margin-top: 2rem;
  text-align: center;
  width: 100%;
}

.gpt3__footer-copyright p {
  font-family: var(--font-family);
  font-size: 12px;
  line-height: 24px;
  color: white;
}

@media screen and (max-width: 850px) {
  .gpt3__footer-heading h1 {
    font-size: 44px;
    line-height: 50px;
  }
}


@media screen and (max-width: 550px) {
    .gpt3__footer-heading h1 {
      font-size: 32px;
      line-height: 42px;
    }

    .gpt3__footer-links div{
        margin: 1rem 0;
    }

    .gpt3__footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
  }
