.gpt3__navbar {
  display: flex;
  align-items: center;
  background: var(--color-footer);

  background:-moz-radial-gradient(circle at 30% -100%, #042c54 50%, rgba(27, 120, 222, 1) 200%);

  /* safari 5.1+,chrome 10+ */
  background:-webkit-radial-gradient(circle at 30% -100%, #042c54 50%, rgba(27, 120, 222, 1) 200%);

  /* opera 11.10+ */
  background:-o-radial-gradient(circle at 30% -100%, #042c54 50%, rgba(27, 120, 222, 1) 200%);

  /* ie 10+ */
  background:-ms-radial-gradient(circle at 30% -100%, #042c54 50%, rgba(27, 120, 222, 1) 200%);

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 30% -100%, #042c54 50%, rgba(27, 120, 222, 1) 200%);

  padding: 2rem 6rem;
}

.gpt3__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.gpt3__navbar-links_logo {
  margin-top: 1rem;
}

.gpt3__navbar-links_logo img {
  width: 35%;
  height: auto;
}

.gpt3__navbar-links_container {
  display: flex;
  flex-direction: row;
}

.gpt3__navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
  color: #fff;
  font-family: var(--font-family);

  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;

  margin: 0 1rem;
  cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
  padding: 0.5rem 1rem;
  color: #fff;
  background: #ff4820;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  border-radius: 5px;
  border: 0;
  outline: none;
  cursor: pointer;
}

.gpt3__navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}

.gpt3__navbar-menu svg {
  cursor: pointer;
}

.gpt3__navbar-menu_container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu_container p {
  margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
  display: none;
}

@media screen and (max-width: 1050px) {
  .gpt3__navbar-links_container {
    display: none;
  }

  .gpt3__navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }
  .gpt3__navbar-sign{
    display: none;
  }
  .gpt3__navbar-menu_container{
    top: 20px;
  }

  .gpt3__navbar-menu_container-links-sign{
    display: block;
  }
}


